import {Component} from '@angular/core';

// import { Header } from '../layout';


/**
 * Component (matadata)
 *
 * selector    string    
 * inputs    string[]    
 * outputs    string[]    
 * host    {[key: string]: string;}    /
 * providers    any[]     ContentChildren
 * exportAs    string    
 * moduleId    string     id
 * queries    {[key: string]: any;}    
 * viewProviders    any[]     ContentChildren
 * changeDetection    ChangeDetectionStrategy    
 * templateUrl    string    
 * template    string    
 * styleUrls    string[]    
 * styles    string[]    
 * animations    AnimationEntryMetadata[]
 * directives    Array    
 * pipes    Array    
 * encapsulation    ViewEncapsulation    
 * interpolation    [string, string]
 */

@Component({
    selector: 'app',
    templateUrl: 'views/layout/layout.html'
    // directives: [Header]
})

export class App {
}




